$main-color: #2c6add;

.loader {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  &__spinner {
    width: 2em;
    height: 2em;
    margin: 1em auto;

    border: 0.3em solid #ddd;
    border-radius: 50%;
    border-left-color: $main-color;
    animation: load8 1.2s infinite linear;

    @keyframes load8 {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }

  &__text {
    display: block;

    text-align: center;
  }
}
